<template>
  <tr class="max-w-screen-md">
    <td class="px-6 py-4 w-2/12 whitespace-nowrap">
      <div class="flex items-center">
        <div class="ml-4">
          <div class="text-sm font-medium text-gray-900">
            {{ task.name }}
          </div>
          <div class="text-sm text-gray-500">
            Id: {{ task.id }}
          </div>
        </div>
      </div>
    </td>
    <td class="px-6 py-4 w-6/12 whitespace-nowrap">
      <!--      <div class="text-sm text-gray-900">{{ task.name }}</div>-->
      <div class="text-sm text-gray-500">{{ task.description }}</div>
    </td>
    <td class="px-6 py-4 w-1/12 whitespace-nowrap">
      <span class="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-green-800">
        Active
      </span>
    </td>
    <td class="px-6 py-4 w-3/12 whitespace-nowrap text-sm text-gray-500">
      <button v-if="!task.completed"
          class="hover:bg-gray-800 w-1/2 hover:text-gray-100 transition p-3 rounded-xl"
          @click="changeTaskStateTo({taskID: task.id, taskNewState: true})"
      >Mark as completed
      </button>
      <button v-else
              class="hover:bg-gray-800 w-1/2 hover:text-gray-100 transition p-3 rounded-xl"
              @click="changeTaskStateTo({taskID: task.id, taskNewState: false})"
      >Unmark as completed
      </button>

      <button
          @click="switchModal"
          class="hover:bg-gray-800 w-1/2 ml-4 hover:text-gray-100 transition p-3 rounded-xl"
      >Edit task</button>
      <EditTask v-bind:toggle-modal="toggleModal" :task="task" :title="task.name" :description="task.description" :close-modal="switchModal"></EditTask>
    </td>
  </tr>
</template>

<script>
import {mapActions} from "vuex";
import EditTask from "@/components/Modals/EditTask";
export default {
  components: {EditTask},
  props: [
    'task',
  ],
  data () {
    return {
      toggleModal: false
    }
  },
  name: "Task",
  methods: {
    ...mapActions('tasks',[
        'changeTaskStateTo'
    ]),
    switchModal() {
      this.toggleModal = !this.toggleModal
    }
  }
}
</script>

