<template>
  <div>
    <div class="mt-8 flex flex-col">
      <div class="-my-2 divide-gray-700 overflow-x-auto sm:-mx-6 sm:px-6 lg:px-8">
        <div class="py-2 align-middle inline-block min-w-full sm:px-6 lg:px-8">
          <div class="shadow overflow-hidden border-b border-gray-200 sm:rounded-lg">
            <!--          <button class="mr-40" @click="doGetAllTasks">{{loading}}</button>-->
            <table class="min-w-full divide-y divide-gray-200">
              <tbody class="bg-white divide-y divide-gray-200">
              <Task v-for="task in activeTasks"
                    :key="task.id"
                    :task="task"
              ></Task>
              </tbody>
            </table>
          </div>
          <div class="shadow mt-10 overflow-hidden border-b border-gray-200 sm:rounded-lg">
            <!--          <button class="mr-40" @click="doGetAllTasks">{{loading}}</button>-->
            <table class="min-w-full divide-y divide-gray-200">
              <tbody class="bg-white divide-y divide-gray-200">
              <Task v-for="task in completedTasks"
                    :key="task.id"
                    :task="task"
              ></Task>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import {mapActions, mapGetters, mapState} from "vuex";
import Task from "@/components/Task";

export default {
  name: "Tasks",
  components: {Task},
  methods: {
    ...mapActions('tasks', [
      'showActiveTasks',
      'doGetAllTasks'
    ]),

  },
  created() {
    // console.log('mounted analog')
    this.doGetAllTasks()
  },
  computed: {
    ...mapState({
      tasks: state => state.tasks.tasks
    }),
    ...mapGetters('tasks', {
      activeTasks: 'activeTasks',
      completedTasks: 'completedTasks'
    })
  },
  // data: () => {
  //   return {
  //     loading: true
  //   }
  // }
}
</script>

